<template>
  <div>
    <Background></Background>
    <div class="signbox container">
      <img src="../assets/favicon.png" class="logo" alt="">
      <div class="col-xs-12 form-group input-group-lg">
        <input type="text" class="form-control margintop20 " placeholder="请输入邮箱" v-model="email">
        <input type="password" class="form-control margintop20 " placeholder="请输入密码" v-model="password">
        <button type="button" class="btn btn-primary btn-lg btn-block margintop20" v-on:click="signin">登录</button>
        <div class="margintop20">
          <i class="fa fa-qq fa-lg pull-left font-black link"></i>
          <i class="fa fa-weibo fa-lg pull-left font-black link"></i>
          <i class="fa fa-weixin fa-lg pull-left font-black link"></i>
          <router-link :to="{ name: 'Signin', params: { userId: 123 }}" class="font-black pull-right link">忘记密码</router-link>
          <router-link :to="{ name: 'Signup', params: { userId: 123 }}" class="font-black pull-right link">注册</router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<style src="../assets/css/signbox.css"></style>
<script>
import Background from '../components/Background.vue';
import router from '../router'

export default {
  name: 'hello',
  data () {
    return {
      'msg': 'msg',
      'email':'',
      'password':''
    }
  },
  components: { 
    Background
  },
  methods: {
    signin: function () {
      var params={
        'email':this.email,
        'password':this.password
      }
      this.$axios.post('/signin', params).then(function (response) {
      	console.log(response)
      	if(response.data.recode==='0000'){
      		router.push({ name: 'Index', params: { 'msg': '登录成功' }})
      	}
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



