<template>
  <div>
    <Background></Background>
    <div class="signbox container">
      <img src="../assets/favicon.png" class="logo" alt="">
      <div class="col-xs-12 form-group input-group-lg">
        <input type="email" class="form-control margintop20" placeholder="请输入邮箱" v-on:focus="initmsg" v-model="email">
        <input type="password" class="form-control margintop20" placeholder="请输入密码" v-on:focus="initmsg" v-model="password">
        <input type="number" class="form-control margintop20" placeholder="请输入验证码" v-on:focus="initmsg" v-model="vercode">
        <div class="alert alert-danger margintop20" role="alert" v-show="msg.show"><b>{{msg.con}}</b></div>
        <button type="button" v-on:click="signup" class="btn btn-primary btn-lg btn-block margintop20">注册</button>
        <div class="margintop20">
          <router-link :to="{ name: 'Signin', params: { userId: 123 }}" class="font-black pull-right link">登录</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<style src="../assets/css/signbox.css"></style>
<script>
import Background from '../components/Background.vue';
import router from '../router'
export default {
  name: 'hello',
  data () {
    return {
      'msg': {'con':'','show':false},
      'email':'',
      'password':'',
      'vercode':''
    }
  },
  components: { 
    Background
  },
  methods: {
    signup: function(){
      if(!this.email){
        this.msg.con='请输入邮箱';
        this.msg.show=true;
        return;
      }else {
        var reg=/^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/;
        if(!reg.test(this.email)){
          this.msg.con='邮箱格式不正确';
          this.msg.show=true;
          return;
        }
      }
      if(!this.password){
        this.msg.con='请输入密码';
        this.msg.show=true;
        return;
      }else {
        if(this.password.length<6){
          this.msg.con='密码不得少于6位';
          this.msg.show=true;
          return;
        }
      }
      if(!this.vercode){
        this.msg.con='验证码';
        this.msg.show=true;
        return;
      }else{
      	if(this.vercode.length!=4){
          this.msg.con='验证码长度不正确';
          this.msg.show=true;
          return;
        }
      }
      this.initmsg();
      console.log(this.$axios);
      var params={
        'email':this.email,
        'password':this.password,
        'vercode':this.vercode
      }
      this.$axios.post('/signup', params).then(function (response) {
      	console.log(response);
      	if(response.data===true){
      		router.push({ name: 'Signin', params: { 'msg': '注册成功，请登录' }})
      	}
      })
    },
    initmsg:function(){
      this.msg.con='';
      this.msg.show=false;
    }
  }
}
</script>
