<template>
<div class="header" >
  <div class="header-hor">
    <div class="header-brand"></div>
    <router-link class="header-link" v-bind:class="{'header-link-active':module=='index'}"  :to="{ name: 'Index'}" >主页</router-link>
    <router-link class="header-link" v-bind:class="{'header-link-active':module=='works'}" :to="{ name: 'Hello'}" >作品</router-link>
    <router-link class="header-link" v-bind:class="{'header-link-active':module=='posts'}" :to="{ name: 'Write'}" >写作</router-link>
    <div class="header-emp">
	  
    </div>
    <router-link class="header-link header-link-btn" :to="{ name: 'Signin'}" >登录</router-link>
    <i class="fa fa-search fa-lg" v-on:click="searchShow=!searchShow"></i>
  </div>
  <div class="input-group input-group-lg" style="margin:20px 10px;" v-show="searchShow"> 	
    <input type="text" class="form-control" placeholder="请输入搜索关键词（站内搜索）" v-model="keyword"></input>
    <span class="input-group-addon" style="cursor:pointer" v-on:click="search"><i class="fa fa-search"></i></span>
  </div>
  <div class="header-ver">
			
  </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      module: 'index',
      searchShow: false,
      keyword: ''
    }
  },
  methods:{
  	search:function(){
  	  var param={
  	  	keyword:this.keyword
  	  }
  	  console.log(param);
  	  this.$axios.post('/search', param).then(function (response) {
      	console.log(response.data)
      	if(response.data.recode==='0000'){
      		console.log("查询成功")
      	}
      })
  	}
  }
}
</script>

