  height:  50px;
  width: 100%;
  background: #f8f8f8;
  border-bottom: 1px solid #eee;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  align-items: center;
  padding:0 20px;
/*  background-image: -webkit-gradient(linear, 50% 0%, 0 100%, from(transparent), color-stop(.5, transparent), color-stop(.5, #648ee1), to(#648ee1)),
  -webkit-gradient(linear, 50% 0, 100% 100%, from(transparent), color-stop(.5, transparent), color-stop(.5, #648ee1), to(#648ee1));
  background-size: 10px 5px;
  background-repeat: repeat-x;
  background-position: 0 100%;*/
}
.header-brand {
	width: 100px;
	height: 100%;
	background: url('../assets/favicon.png') no-repeat ;
	background-size: 40px 40px;
	background-position: 50% 50%;
}
.header-link{
	width: 100px;
	height: 50px;
	line-height: 50px;
	font-size: 18px;
	color: #666666;
	text-align: center;
	/*background: #648ee1;*/
	text-decoration: none !important;
}
.header-link:hover{
	color: #000;
	background: #eee;
}

.header-link-active{
	color: #666;
	background: #eee;
	border-right: 1px solid #e8e8e8;
	border-left: 1px solid #e8e8e8;
}
.header-link-btn{
	height: 34px;
	width: 45px;
	line-height: 34px;
	font-size: 14px;
	border: 1px solid #ccc;
	border-radius: 3px;
	margin: 10px;
}
.header-emp{
	flex: 1;
	width: 100%;
}

