<template>
  <div class="panel panel-default write-panel">
    <div class="panel-heading" style="padding-left:0;padding-right:0;">
      <div class="col-xs-12 col-sm-6 col-md-6" style="margin-top:10px">
        <div class="input-group">
          <span class="input-group-addon">Title</span>
          <input type="text" class="form-control" placeholder="请输入标题" v-model="title">
        </div>
      </div>
      <div class="col-xs-6 col-sm-3 col-md-3" style="margin-top:10px">
        <div class="input-group">
          <span class="input-group-addon">Category</span>
          <select class="form-control" style="border-radius:0" v-model="category">
          	<option value="" v-for="option in categories" v-bind:value="option">{{option}}</option>
          </select>
        </div>
      </div>
      <div class="col-xs-6 col-sm-3 col-md-3" style="margin-top:10px">
        <div class="input-group">
          <span class="input-group-addon">Tags</span>
          <input type="text" class="form-control" placeholder="请输入标签" v-model="tags">
        </div>
      </div>
    </div>
    <div class="post-area">
      <textarea class="form-control  write-area" id="content" v-bind:style="{fontSize:fontSize+'px'}" v-model="content"></textarea>
      <div class="preview-area" id="preview" readonly="" v-show="previewShow"></div>
    </div>
    <div class="panel-footer">
      <div class="col-sm-4 col-xs-12 text-center" style="margin-bottom:10px">
        <!-- <div class="btn-toolbar "> -->
          <div class="btn-group" >
            <button class="btn btn-default" v-on:click="fullscreenToggleWrite"><i class="fa fa-arrows-alt"></i> 专注</button>
            <button class="btn btn-default" v-on:click="fullscreenToggle"><i class="fa fa-tv"></i> 全屏</button>
            <button class="btn btn-default" v-on:click="previewShow=!previewShow"><i class="fa fa-columns"></i> 预览</button>
          </div>
        </div>
      <!-- </div> -->
      <div class="col-sm-4 col-xs-12 text-center" style="margin-bottom:10px">
        <!-- <div class="btn-toolbar"> -->
          <div class="btn-group">
            <button class="btn btn-default" v-on:click="upload"><i class="fa fa-photo"></i> 传图</button>
            <button class="btn btn-default" v-on:click="fontSize++"><i class="fa fa-search-plus"></i> 放大</button>
            <button class="btn btn-default" v-on:click="fontSize--"><i class="fa fa-search-minus"></i> 缩小</button>
          </div>
        <!-- </div> -->
      </div>
      <div class="col-sm-4 col-xs-12 text-center" style="margin-bottom:10px">
        <!-- <div class="btn-toolbar"> -->
          <div class="btn-group">
            <button class="btn btn-default" v-on:click="publish"><i class="fa fa-trash-o"></i> 删除</button>
            <button class="btn btn-default" v-on:click="publish"><i class="fa fa-save"></i> 保存</button>
            <button class="btn btn-default" v-on:click="publish"><i class="fa fa-send-o"></i> 发布</button>
          </div>
        <!-- </div> -->
      </div>
    </div>
  </div>
</template>
<script>
import router from '../router'
import '../assets/js/markdown.js'
import '../assets/js/screenfull.js'
export default {
  data () {
    return {
      'title': '',
      'category':'',
      'tags':'',
      'content':'',

      'preview':'',
      'previewShow':false,
      'fontSize': 14,
      'categories':[
        'Linux',
        'FrontEnd',
        'BackEnd'
      ]
    }
  },
  watch: {
    content: function (val, oldVal) {
      $('#preview').html(markdown.toHTML(val));
    }
  },
  methods: {
    publish: function () {
      if(!this.title){
      	alert("请输入标题");
      	return;
      }
      if(!this.category){
      	alert("请选择类别");
      	return;
      }
      if(!this.content){
      	alert("请输入内容");
      	return;
      }
      var params={
        'title':this.title,
        'category':this.category,
        'content':this.content,
        'tags':this.tags.split(';')
      }
      console.log(params)
      this.$axios.post('/publish', params).then(function (response) {
      	console.log(response)
      	if(response.data.recode==='0000'){
      		router.push({ name: 'Index', params: { 'msg': '提交成功' }})
      	}else {
      		alert(response.data.msg);
      	}
      },function(response){
      	alert('系统错误，请稍后重试')
      })
    },
    fullscreenToggleWrite:function(){
   	  if(screenfull.enabled){
        screenfull.toggle($('#content')[0]);
      }
    },
    fullscreenToggle:function(){
      if(screenfull.enabled){
        screenfull.toggle();
      }
    },
    upload:function(){
    	console.log("待完善");
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.write-panel {
	position: fixed;
	top: 0;
	height: 100%;
	left: 0;
	width: 100%;
	display: flex;
	flex-direction: column;
}
.post-area {
	flex: 1;
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: row;
}
.write-area {
	position: relative;
	flex: 1;
	height: 100%;
	width: 100%;
	resize: none;
}
.preview-area {
	position: relative;
	flex: 1;
	height: 100%;
	width: 100%;
	padding: 10px;
	overflow: scroll;	
}
</style>



